@import url(https://fonts.googleapis.com/css?family=Quicksand:500,700);@import url(https://cdnjs.cloudflare.com/ajax/libs/material-design-icons/3.0.1/iconfont/material-icons.min.css);@import url(https://fonts.googleapis.com/css?family=Quicksand:500);
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 15px;
}
html *, body * {
    font-family: 'Quicksand', sans-serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.background {
  position: fixed;
  top: -50px;
  left: -50px;
  right: -50px;
  bottom: -50px;
  background-size: cover;
  background-position: center;
  background-color: #673AB7;
  background-image: url("https://images.pexels.com/photos/1027393/pexels-photo-1027393.jpeg?auto=compress&cs=tinysrgb&h=650&w=940 1x, https://images.pexels.com/photos/1027393/pexels-photo-1027393.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940 2x");
  -webkit-filter: blur(10px);
          filter: blur(10px);
  z-index: -1;
}
a {
  text-decoration: none;
}
#app {
  position: relative;
  top: 80px;
  padding: 15px;
  width: 100%;
  max-width: 600px;
  margin: auto;
}
#app .input--create {
    color: white;
    width: 100%;
    height: 60px;
    background: rgba(255, 255, 255, 0.2);
    font-size: 20px;
    padding: 0 20px;
    border: none;
    border-radius: 3px;
    outline: none;
    margin-bottom: 10px;
}
#app input::-webkit-input-placeholder {
    color: white;
}
#app input:-ms-input-placeholder {
    color: white;
}
#app input::-ms-input-placeholder {
    color: white;
}
#app input::placeholder {
    color: white;
}
.container {
  position: relative;
  /*   width: 600px; */
  padding: 15px;
  padding-bottom: 5px;
  border-radius: 3px;
  margin-bottom: 100px;
}
.todoList {
  margin: 0;
  padding: 0;
}
.todoList li {
    position: relative;
    background-color: rgba(255, 255, 255, 0.2);
    list-style: none;
    /*     height: 60px; */
    line-height: 20px;
    margin-bottom: 8px;
    padding: 20px 20px;
    border-radius: 3px;
    color: #fff;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.todoList li:hover .modify {
      display: block;
}
.filter {
  position: relative;
}
.filter a {
    font-size: 13px;
    background: rgba(255, 255, 255, 0.4);
    color: white;
    padding: 6px 10px;
    display: inline-block;
    border-radius: 2px;
    margin-bottom: 10px;
}
.filter .right {
    float: right;
}
@media only screen and (max-width: 480px) {
.filter .right {
        float: none;
}
}
.filter .right span {
      color: white;
      padding-left: 10px;
}
.modify {
  float: right;
  line-height: 20px;
  display: none;
}
.edit--input {
  position: absolute;
  left: 0;
  height: 100%;
  top: 0;
  width: 100%;
  padding: 0 20px;
  font-size: 16px;
  border: none;
  background: white;
  border: 1px solid #699832;
  border-radius: 3px;
  outline: none;
}
.filter--input {
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
  font-size: 18px;
  color: white;
  padding: 0 20px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 3px;
  outline: none;
}
.completed--icon, .edit--icon, .remove--icon {
  line-height: 20px;
  vertical-align: top;
  font-size: 20px;
  padding-right: 5px;
}
.selected {
  background: rgba(255, 255, 255, 0.2) !important;
}
.completed {
  background-color: rgba(139, 195, 74, 0.8) !important;
  color: white !important;
}
.completed.edit--input {
    background-color: #8BC34A !important;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
